/* ---------------------------------------- */
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { width: 100%; height: 120px; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer { height: 80px; text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

.main-wrapper { padding: 0 0 0 0; background-image: url("../img/bg.jpg"); min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 1200px; padding: 0 15px 0 15px; width: 100%; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 13px; line-height: 18px; color: #000; padding: 15px 0 15px 0; font-weight: 400; }

h1 { font-size: 36px; line-height: 42px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h2 { font-size: 32px; line-height: 37px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h3 { font-size: 28px; line-height: 32px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.top { width: 100%; height: 120px; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; }

.logo { height: 120px; display: flex; align-items: center; }

.logo h1 { font-size: 30px; font-family: 'ComfortaaBold'; color: #2b2b2b; letter-spacing: -1.5px; padding: 0; }

.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; }

.flex-container { display: flex; justify-content: space-between; }

.sidebar { width: 23.08%; margin-right: 2.57%; }

.check { transform: rotate(180deg); }

.input-color, .input-size { background: transparent; background-color: #f3f3f3; padding: 0; text-indent: 13px; height: 28px; width: 68%; font-family: 'OpenSansRegular',sans-serif; margin-right: -8px; border-radius: 4px; text-transform: capitalize; font-size: 14px; border: 1px solid #e1e1e1; cursor: pointer; }

.accordion { border: 1px solid #e1e1e1; border-bottom: none; }

.accordion__head { background: #fbfbfb; display: flex; justify-content: space-between; align-items: center; padding: 0 15px; height: 40px; border-bottom: 1px solid #e1e1e1; cursor: pointer; overflow: hidden; }

.accordion__head h2 { color: #343434; font-size: 14px; font-family: Arial, Helvetica, sans-serif; font-weight: bold; }

.accordion__head .arrow { height: 6px; width: 10px; z-index: 1000; position: absolute; left: 21.3%; background: url("../img/icons/rectangle.png") no-repeat; }

.accordion__body { background: #fbfbfb; display: none; font-size: 13px; border-bottom: 1px solid #e1e1e1; overflow: hidden; }

.material, .category, .color, .size { padding: 15px 30px 0; height: 100%; }

.material__item, .category__item, .color__item, .size__item { color: #444444; list-style-type: none; margin: 0; font-size: 13px; line-height: 13px; padding: 7px 0; font-weight: normal; font-family: Arial, Helvetica, sans-serif; cursor: pointer; }

.material .jq-checkbox, .category .jq-checkbox, .color .jq-checkbox, .size .jq-checkbox { width: 15px; height: 15px; background: #39a6be; margin-right: 3px; vertical-align: -3px; }

.material .jq-checkbox.checked .jq-checkbox__div, .category .jq-checkbox.checked .jq-checkbox__div, .color .jq-checkbox.checked .jq-checkbox__div, .size .jq-checkbox.checked .jq-checkbox__div { width: 16px; height: 16px; margin: 0; }

.material .jq-checkbox.checked, .category .jq-checkbox.checked, .color .jq-checkbox.checked, .size .jq-checkbox.checked { background: #39a6be; }

.material .active, .category .active, .color .active, .size .active { color: #39a6be; font-weight: bold; }

.check__list { color: #444444; list-style-type: none; margin: 0; font-size: 13px; line-height: 13px; padding-bottom: 15px; font-weight: normal; font-family: Arial, Helvetica, sans-serif; cursor: pointer; }

.button { color: #36a7bf; text-align: center; margin: 0 auto; width: 100%; padding: 10px 0; background-color: #fff; border: 1px solid #e1e1e1; border-top: none; }

.mCS-blue-theme.mCSB_scrollTools { margin: 15px 0; }

.mCSB_draggerContainer { height: 65px; }

.mCS-blue-theme.mCSB_scrollTools .mCSB_dragger { height: 65px !important; }

.mCS-blue-theme.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar { background-color: #93deff; height: 65px; width: 3px; }

.mCS-blue-theme.mCSB_scrollTools .mCSB_draggerRail { background-color: transparent; height: 65px; }

.mCustomScrollBox { margin-right: 2px; }

.main-content { width: 74.3%; }

.products { display: flex; flex-wrap: wrap; justify-content: flex-start; margin-bottom: 22px; }

.item { width: 268px; max-height: 380px; text-align: center; margin: 0 3.7% 1.709% 0; border-radius: 4px; box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.09); background-color: #fff; transition: box-shadow .5s linear; }

.item__review { width: 84px; height: 15px; margin: 0 auto; background-image: url("../img/icons/star-empty.png"); }

.item__review div { height: 100%; background-image: url("../img/icons/star-full.png"); }

.item__img { margin-bottom: 20px; position: relative; }

.item__img .sale { position: absolute; top: 11px; left: 13px; padding: 10px 5px; font-size: 11px; line-height: 11px; color: #ffffff; font-family: 'OpenSansSemiBold',sans-serif; background-color: #48b3cb; text-transform: uppercase; border-radius: 3px; }

.item__img .new { background-color: #48b3cb; }

.item__img .hit { background-color: #ffae2e; }

.item__img img { max-width: 100%; max-height: 234px; overflow: hidden; z-index: -1; }

.item__title { font-size: 15px; line-height: 15px; color: #282828; font-family: "OpenSansRegular",sans-serif; display: inline-block; position: relative; transition: color .5s linear; margin-top: 5px; }

.item__title:after { content: ""; height: 1px; width: 80px; background-color: #dbdbdb; display: block; position: absolute; bottom: -5px; left: 50%; transform: translateX(-50%); }

.item__price { font-size: 15px; padding: 22px 0 20px 0; color: #282828; font-family: "OpenSansBold",sans-serif; }

.item:hover { cursor: pointer; box-shadow: 0px 2px 12px 2px rgba(68, 190, 217, 0.36); }

.item:hover .item__title { color: #23a2be; }

.item:nth-child(3n) { margin-right: 0; }

.related__title { font-size: 16px; text-transform: uppercase; color: #282828; font-family: "OpenSansRegular",sans-serif; padding: 0 0 10px 0; }

.slick-slider { background: transparent; text-align: center; }

.slick-slider .item { margin-right: 29px; margin: 4px 14px; }

.slick-slider .slick-arrow { background-color: #f1f1f1; background-position: center; height: 37px; width: 37px; border: 1px solid #dddddd; border-radius: 50%; top: 94.4%; z-index: 100; position: absolute; }

.slick-slider .prev-arrow { background-image: url("../img/icons/next-arrow.png"); background-repeat: no-repeat; right: 42%; }

.slick-slider .next-arrow { background-image: url("../img/icons/arr.png"); background-repeat: no-repeat; left: 42%; }

.slick-slider .next-change { left: 10%; transform: translateY(-100%); }

.slick-slider .prev-change { transform: translateY(-100%); right: 10%; }

.slick-slider .slick-dots { text-align: center; margin-top: 45px; display: inline-block; }

.slick-slider .slick-dots li button { padding: 0; margin: 0; background: transparent; font-size: 0; border: none; }

.slick-slider .slick-dots li { list-style-type: none; display: inline-block; margin: 0 2px; }

.slick-slider .slick-dots li button:before { content: "\2022"; color: #ccc; font-size: 18px; }

.slick-slider .slick-dots li.slick-active button:before { content: "\2022"; color: #e44256; font-size: 18px; }

@media screen and (max-width: 1200px) { .item { width: 31%; margin: 0 3.33% 20px 0; }
  .slick-slider .item { margin: 4px 5px; } }

@media screen and (max-width: 991px) { .products { flex-wrap: wrap; justify-content: flex-start; }
  .products__item { margin-bottom: 20px; width: 31%; }
  .products .accordion__body { border-bottom: none; }
  .products .accordion__body .category, .products .accordion__body .size, .products .accordion__body .color, .products .accordion__body .material { padding: 0px; }
  .products .accordion__group { width: 50%; } }

@media screen and (max-width: 991px) and (max-width: 767px) { .products { flex-wrap: wrap; justify-content: space-between; }
  .item { margin: 0 auto 20px; width: 44%; }
  .item__img { margin-bottom: 10px; }
  .item__img .sale { font-size: 8px; padding: 5px; }
  .item__review { height: 12px; width: 64px; background-size: contain; margin: 0 auto; }
  .item__review div { background-size: contain; }
  .item__title { font-size: 14px; padding: 5px 0; }
  .item__price { padding: 14px 0 4px; font-size: 12px; }
  .item:nth-child(2n) { margin-right: auto; }
  .item:nth-child(3n) { margin-right: auto; }
  .flex-container { flex-direction: column; }
  .sidebar { width: 100%; padding: 0 3%; margin-bottom: 30px; }
  .accordion { display: flex; flex-wrap: wrap; flex-direction: row; justify-content: space-between; }
  .accordion__head { position: relative; width: 100%; border-bottom: 0; padding-right: 0; padding-left: 5px; border-bottom: 1px solid #e1e1e1; }
  .accordion__head .arrow { position: absolute; left: 88%; }
  .accordion__head input { width: 40%; margin: 0; }
  .accordion__body { border-bottom: none; }
  .accordion__body .category, .accordion__body .size, .accordion__body .color, .accordion__body .material { padding: 5px; }
  .accordion__group { width: 50%; }
  .main-content { width: 100%; }
  .slick-slider .item { margin: 0 auto 20px; width: 42%; }
  .slick-slider .slick-arrow { top: 95%; } }

@media screen and (max-width: 991px) and (max-width: 500px) { .main-content .products { flex-wrap: wrap; justify-content: center; }
  .main-content .item { margin-bottom: 30px; width: 70%; }
  .slick-slider .slick-arrow { top: 95%; }
  .slick-slider .next-arrow { left: 30%; }
  .slick-slider .prev-arrow { right: 30%; } }
